
/* ===================================
    Loader Styles  
====================================== */
.loader-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999999;
    display: flex;
    .main-loader
    {
        width : 200px;
        height: 200px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 12%;
        text-align: center;
        img
        {
            width : 100%;
            display : block;
            
        }
        h5
        {
            font-family: 'Lato', sans-serif;
            text-transform: uppercase;
            font-weight: 400;
           font-size: 15.5px;
           line-height: 144.19%;
            letter-spacing: 0.21em;
            color : #0e0e0e;
        }
    }
  }